@media only screen and (min-width: 150px) and (max-width: 767px) {
    .App {
        display: grid;
        grid-template-columns: .3fr 1fr .3fr;
        grid-template-rows: .25fr .5fr .1fr;
    }

    nav {
        grid-column: 1/4;
        grid-row: 1/3;
        text-align: center;
    }

    header {
        grid-column: 2/3;
    }

    img {
        height: 250px;
    }

    main {
        grid-column: 2/3;
    }

    .BioPage {
        grid-column: 2/3;
        grid-row: 4/5;
        background: linear-gradient(to top, transparent, rgb(188, 248, 228) 75%);
        border-radius: 25px;
    }

    .BioPageBody {
        margin: 5px 10px;
    }

    .APIPage {
        grid-column: 2/3;
        grid-row: 4/5;
        margin: 15px 0 0 0;
        border-radius: 25px;
        background: linear-gradient(to top, transparent, rgb(149, 209, 250) 75%);
    }

    .APIPageBody {
        margin: 10px 10px;
    }

    .Location {
        grid-column: 2/3;
    }

    .CovidStats {
        background: linear-gradient(to top, transparent, rgb(206, 206, 206) 75%);
        grid-column: 2/3;
        grid-row: 2/3;
        padding: 5px 0 5px 0;
        text-align: center;
    }

    .Covid {
        background-color: rgb(141, 141, 141);
        margin: -5px 0 0 0;
    }

    .Cases {
        padding: 5px 0 5px 0;
        text-align: center;
    }

    .Vaccinated {
        padding: 5px 0 5px 0;
        text-align: center;
    }

    .Deaths {
        padding: 5px 0 5px 0;
        text-align: center;
    }

    footer {
        grid-column: 2/3;
        grid-row: 6/7;
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        text-align: center;
    }
};
